import Checkbox from "../../../Checkbox";
import React from "react";

export function CheckboxSearchParam({handleCheckboxChange, applyFilters, checkboxes})
{
    return (
        <div className="col-12">
            <h6 className='mb-3'> </h6>
            {
                [
                    {value: 'searchBooks', label: ''},
                    {value: 'searchPeriodicals', label: ''},
                    {value: 'searchAudio', label: ''},
                    {value: 'searchVideo', label: ''},
                    {value: 'searchArchives', label: ''}
                ].map(({value, label}) => (
                    <Checkbox
                        key={value}
                        id={value}
                        label={label}
                        isChecked={checkboxes?.[value]}
                        handleCheckboxChange={handleCheckboxChange}
                        applyFilters={applyFilters}
                    />
                ))
            }
        </div>
    )
}